import Vue from 'vue'
import Router from 'vue-router'
import recharge from '@/components/recharge'
// import regulation from '@/components/regulation'
Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'recharge',
      component: recharge
    }
  ]
})



